<template>
  <div>
    <Header headerT="书影音">
      <img src="../../assets/images/ic_actionbar_search_icon.png" slot="leftImg" class="leftImg">
      <img src="../../assets/images/ic_create_group_chat_blue.png" slot="rightImg" class="rightImg">
    </Header>
    <div class="audio-tab" >
        <span @click="tabindex=0" :class='{aborder:tabindex==0}' >
          <router-link to="/audio/mv">电影</router-link>
        </span>
        <span @click="tabindex=1" :class='{aborder:tabindex==1}'>
          <router-link to="/audio/read">读书</router-link>
        </span>
        <span @click="tabindex=2" :class='{aborder:tabindex==2}'>
          <router-link to="/audio/mv">电视</router-link>
        </span>
        <span @click="tabindex=3" :class='{aborder:tabindex==3}'>
          <router-link to="/audio/tc">同城</router-link>
        </span>
        <span @click="tabindex=3" :class='{aborder:tabindex==4}'>
          <router-link to="/audio/music">音乐</router-link>
        </span>
    </div>

    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>

  import Header from '../../components/header.vue'
  export default {
    components:{
      Header
    },
    data:function () {
      return{
        tabindex:0
      }
    }
  }

</script>


<style>
  .audio-tab{
    width: 100%;
    height: 40px;
  }
  .audio-tab{
    width: 100%;
    height: 40px;
    font-size: 0;
  }
  .audio-tab span a{
    width: 20%;
    display: inline-block;
    float: left;
    color: #999;
    font-size: 14px;
    box-sizing:border-box;
    text-decoration: none;
    text-align: center;
    line-height:38px;
  }
  .audio-tab  .aborder a{
    text-align: center;
    color: #68CB78;
    border-bottom:2px solid #68CB78;
    line-height:38px;
  }
</style>



